.category-card {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 150px;
    width: 150px;
    border: 2px solid #e2e3e5;
    border-radius: 50%;
    color: #003466;
    margin: 10px;
}

.category-card p {
    align-self: center;
    text-align: center;
    margin: 0;
    max-width: 120px;
}

.category-card__icon {
    font-size: 42px;
}

.category-card:hover {
    cursor: pointer;
    color: #154985;
    border: 2px solid #154985;
}